<template>
  <nav id="nav">
    <!-- Menu top bar -->
    <div
      :class="[
        !onProgramme || (onProgramme && menuOpen)
          ? 'home-background'
          : 'programme-background',
      ]"
      class="menu-top-bar"
    >
      <div class="container d-flex justify-content-between mt-3 pb-3 px-md-0">
        <div class="menu-button ms-md-2 ms-0">
          <div class="row flex-nowrap">
            <div @click="menuOpen = !menuOpen" class="col-auto pe-0">
              <span v-if="!menuOpen">Menu</span
              ><span v-else class="faded-text">
                {{ $t("furniture.close") }}</span
              >
            </div>
            <div v-if="!menuOpen" class="col-auto p-0 d-none d-md-block">
              <img
                :style="
                  onProgramme
                    ? 'filter: brightness(0) saturate(100%) invert(14%) sepia(36%) saturate(3208%) hue-rotate(180deg) brightness(98%) contrast(102%);'
                    : ''
                "
                @click="showLang = !showLang"
                class="world-icon ms-3"
                src="@/assets/img/icons/world.svg"
                alt=""
              />
            </div>
            <div class="lang-wrapper p-0 d-none d-md-block">
              <transition name="lang">
                <div v-if="showLang" class="ms-3 col-auto p-0">
                  <span @click.prevent="setLocale('it')">IT</span> -
                  <span @click.prevent="setLocale('en')">EN</span>
                </div></transition
              >
            </div>
          </div>
        </div>
        <div v-if="!menuOpen" class="page-title d-none d-md-block">
          {{ pageTitle }}
        </div>
        <div class="status">
          <span v-if="onAir" class="me-2">ON AIR</span>
          <span v-if="!onAir" class="me-2">OFF AIR</span>
          <div v-if="onAir" class="red-dot me-2"></div>
          <span v-if="onAir">
            <span
              v-if="!audioPlaying"
              @click="playAudio"
              class="me-2 player-button"
              >Play</span
            >
            <span v-if="audioPlaying" @click="pauseAudio" class="player-button"
              >Pause</span
            >
          </span>
        </div>
      </div>
    </div>
    <!-- Menu dropdown -->
    <transition name="dropdown">
      <div
        v-if="menuOpen"
        :class="[onProgramme ? 'programme-background' : 'home-background']"
        class="menu-dropdown"
      >
        <div class="container mt-5 pt-4">
          <div class="row">
            <router-link class="col-12" to="/">Home</router-link>

            <router-link class="col-12" :to="`/${$i18n.locale}/programme`">{{
              $t("furniture.programme")
            }}</router-link>

            <div @click="scrollToInstallationView" class="col-12 link">
              Installation View
            </div>

            <div @click="scrollToNewsletter" class="col-12 link">
              Newsletter
            </div>

            <div @click="scrollToInfo" class="col-12 link">
              Info &#38; Visit
            </div>

            <div class="lang-switch d-block d-md-none">
              <div
                @click.prevent="setLocale('it')"
                :class="[$i18n.locale === 'it' ? '' : 'faded-text']"
                class="col-12"
              >
                ITALIANO
              </div>
              <div
                @click.prevent="setLocale('en')"
                :class="[$i18n.locale === 'en' ? '' : 'faded-text']"
                class="col-12"
              >
                ENGLISH
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="fade">
      <div v-if="menuOpen" class="background-blur"></div
    ></transition>
    <iframe
      class="youtube-audio"
      width="0"
      height="0"
      :src="
        'https://www.youtube.com/embed/' +
        this.audioLink +
        '?enablejsapi=1&version=3&playerapiid=ytplayer'
      "
      modestbranding="0"
      controls="0"
      frameborder="0"
      allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
      allowfullscreen
    ></iframe>
  </nav>
</template>
<script>
export default {
  data() {
    return {
      showLang: false,
      menuOpen: false,
      pageTitle: "A STORM IS BLOWING FROM PARADISE",
      onProgramme: false,
      audioPlaying: false,
      audioLink: [],
      onAir: false,
    };
  },
  mounted() {
    this.setTitle();
    let prevScrollpos = window.pageYOffset;
    window.onscroll = () => {
      let currentScrollPos = window.pageYOffset;
      if (!this.menuOpen) {
        if (prevScrollpos > currentScrollPos || window.scrollY == 0) {
          document.getElementById("nav").style.top = "0";
          document.getElementsByClassName("menu-top-bar")[0].style.background =
            this.menuBarColour;
        } else {
          document.getElementById("nav").style.top = "-6rem";
        }

        prevScrollpos = currentScrollPos;

        if (currentScrollPos <= 0 || window.scrollY == 0) {
          document.getElementsByClassName("menu-top-bar")[0].style.background =
            "rgba(0, 0, 0, 0)";
        }
      } else return;
    };
  },
  computed: {
    menuBarColour() {
      if (this.onProgramme) {
        return "rgba(221, 207, 178, 1)";
      } else return "rgba(5, 70, 113, 1)";
    },
  },
  methods: {
    async getData() {
      const data = await this.$prismic.client.getSingle("furniture");
      this.audioLink = data.data.audio_livestream_link.substring(
        data.data.audio_livestream_link.indexOf("=") + 1
      );
      this.onAir = data.data.livestream_active;
    },
    setTitle() {
      if (this.$route.name === "programme") {
        this.onProgramme = true;
        this.pageTitle = this.$t("furniture.public");
      } else {
        this.onProgramme = false;
        this.pageTitle = "A STORM IS BLOWING FROM PARADISE";
      }
    },
    setLocale(locale) {
      if (this.$i18n.locale !== locale) {
        localStorage.setItem("language", locale);
        this.$i18n.locale = localStorage.getItem("language");
        this.$router.push({
          params: {
            lang: locale,
          },
        });
      }
      this.showLang = false;
      this.menuOpen = false;
    },
    scrollToInstallationView() {
      this.menuOpen = false;
      this.$router.push("/" + this.$i18n.locale);
      setTimeout(() => {
        console.log(document.getElementById("installation-gallery"));
        let height = document.getElementById("installation-gallery").offsetTop;

        window.scrollTo(0, height);
      }, 300);
    },
    scrollToNewsletter() {
      this.menuOpen = false;
      let height = document.getElementById("newsletter").offsetTop;

      window.scrollTo(0, height);
    },
    scrollToInfo() {
      this.menuOpen = false;
      let height = document.getElementById("footer").offsetTop;

      window.scrollTo(0, height);
    },
    playAudio() {
      this.audioPlaying = true;
      let embed = document.getElementsByClassName("youtube-audio")[0];
      embed.contentWindow.postMessage(
        '{"event":"command","func":"' + "playVideo" + '","args":""}',
        "*"
      );
      embed.contentWindow.postMessage(
        '{"event":"command","func":"' + "unMute" + '","args":""}',
        "*"
      );
      embed.contentWindow.postMessage(
        '{"event":"command","func":"' + "setVolume" + '","[100]":""}',
        "*"
      );
    },
    pauseAudio() {
      this.audioPlaying = false;
      document
        .getElementsByClassName("youtube-audio")[0]
        .contentWindow.postMessage(
          '{"event":"command","func":"' + "pauseVideo" + '","args":""}',
          "*"
        );
    },
  },
  created() {
    this.getData();
  },
  watch: {
    $route() {
      this.setTitle();
      this.menuOpen = false;
    },
    menuOpen() {
      if (this.menuOpen) {
        document.body.style.overflow = "hidden";
      } else {
        document.body.style.overflow = "auto";
      }
    },
  },
};
</script>

